.page {  
    width: 100%;
    background: #000000;
    font-family: "Inter", "Arial", "sans-serif";
    margin: 0 auto;
   }  

.header {
    margin: 0 auto;
    max-width: 880px;
    min-height: 74px;
    display: flex;
    justify-content: flex-start;
    flex-direction: column;
    align-items: flex-start;
    padding-top: 45px;
    opacity: 0.7;
    border-bottom: 1px solid #545454;
}

@media screen and (max-width: 1279px) {
.header {
    max-width: 850px;
  }
}

@media screen and (max-width: 1023px) {
.header {
    max-width: 700px;
    padding-top: 35px;
  }
}

@media screen and (max-width: 767px) {
.header {
    max-width: 420px;
  }
}

@media screen and (max-width: 450px) {
.header {
    max-width: 320px;
    min-height: 56px;
    padding-top: 28px;
  }
}
.header__logo {
    max-width: 142px;
    max-height: 33px;
  }

@media screen and (max-width: 450px) {
.header__logo {
    max-width: 103.74px;
    max-height: 24.4px;
    padding-left: 27px;
  }
}
.profile {
    max-width: 880px;
    min-height: 120px;
    display: flex;
    margin: 40px auto;
  }

@media screen and (max-width: 1023px) {
.profile {
    max-width: 780px;
  }
}

@media screen and (max-width: 850px) {
.profile {
    max-width: 720px;
  }
}

@media screen and (max-width: 767px) {
.profile {
    flex-direction: column;
    max-width: 336px;
    min-height: 286px;
    margin: 42px auto 0;
  }
}

@media screen and (max-width: 450px) {
.profile { 
    max-width: 282px;
  }
}
.profile__avatar {
    max-width: 120px;
    height: 120px;
    display: flex;
    margin-left: 0;
    object-fit: cover;
    border-radius: 50%;
    padding: 0;
    position: relative;
  }


@media screen and (max-width: 767px) {
.profile__avatar {
    margin: 0 auto;
  }
} 
.profile__info {
    margin: 22px 0 0 30px;
    max-width: 336px;
    max-height: 73px;
    display: grid;
    grid-template-columns: repeat(2, minmax(-webkit-min-content, -webkit-max-content));
    grid-template-columns: repeat(2, minmax(min-content, max-content));
  }

@media screen and (max-width: 450px) {
.profile__info {
    margin: 26px 19px 36px;
    max-width: 282px;
    max-height: 54px;
  }
} 
.profile__title {
    font-weight: 500;
    font-size: 42px;
    line-height: 114%;
    margin: 0;
    max-width: 294px;
    align-self: center;
    font-family: "Inter", "Arial", "sans-serif";
    font-style: normal;
    color: #FFFFFF;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden; 
  } 

@media screen and (max-width: 850px) {
.profile__title {
    font-size: 40px;
    max-width: 285px;
  }
}

@media screen and (max-width: 767px) {
.profile__title {
    font-size: 37px;
  }
}

@media screen and (max-width: 450px) {
.profile__title {
    font-size: 27px;
    max-width: 189px;
    min-height: 26px;
    margin: 0 auto;
    margin-left: 27px;
    line-height: 33px;
    text-align: center;
  }
}
.profile__subtitle {
    max-width: 336px;
    font-weight: 400;
    font-size: 18px;
    margin: 16px 0 0;
    font-family: "Inter", "Arial", "sans-serif";
    font-style: normal;
    line-height: 22px;
    color: #FFFFFF;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    align-self: center;
  }

@media screen and (max-width: 767px) {
.profile__subtitle {
    margin: 0 auto;
    margin-right: 10px;
    max-width: 282px;
    line-height: 19px;
    text-align: center;
    margin-top: 14px;
  }
}

@media screen and (max-width: 450px) {
.profile__subtitle {
    font-size: 14px;
    line-height: 17px;  
  }
}
.profile__edit-button {
    width: 24px;
    height: 24px;
    align-self: end;
    cursor: pointer;
    margin: 0 0 7px 18px;
    background-color: #000;
    border: 1px solid #fff;
    transition: opacity 0.2s ease;
    background-image: url(/mesto-react/static/media/Vector2.6bfdb354.svg);
    background-size: 10px;
    background-position: center;
    background-repeat: no-repeat;
    flex-shrink: 0;
  }
  
.profile__edit-button:hover {
    opacity: 0.6;
  }

@media screen and (max-width: 850px) {
.profile__edit-button {
    width: 24px;
    height: 24px;
    margin: 12px 0 7px 16px;
  }
}

@media screen and (max-width: 450px) {
.profile__edit-button {
    width: 18px;
    height: 18px;
    margin: 0 0 6px 10px;
    background-size: 7.5px;
  }
}
.profile__add-button {
    min-width: 150px;
    min-height: 50px;
    align-self: center;
    cursor: pointer;
    background-color: #000;
    border: 2px solid #FFFFFF;
    transition: opacity 0.2s ease;
    box-sizing: border-box;
    border-radius: 2px;
    margin: auto 0 auto auto;
    background-image: url(/mesto-react/static/media/Vector1.b2fce4ac.svg);
    background-size: 22px;
    background-position: center;
    background-repeat: no-repeat;
  }

.profile__add-button:hover {
    opacity: 0.6;
  }

@media screen and (max-width: 767px) {
.profile__add-button {
    margin: 36px auto 0;
    min-width: 282px;
    background-size: 19px;
  }
}

@media screen and (max-width: 450px) {
.profile__add-button {
    margin: 0 19px 0;
    background-size: 16px;
  }
}
.elements {
    margin: 0 auto;
    display: grid;
    grid-template-rows: repeat(2, 1fr);
    grid-template-columns: repeat(auto-fit, minmax(282px, 1fr));
    grid-gap: 17px;
    gap: 17px;
    max-width: 880px;
    min-height: 744px;
    margin-top: 51px;
    box-sizing: border-box;
  } 

@media screen and (max-width: 880px) {
.elements {
    grid-template-rows: repeat(3, 1fr);
    grid-template-columns: repeat(auto-fit, minmax(282px, 1fr));
    max-width: 581px;
  }
}

@media screen and (max-width: 767px) {
.elements {
    margin: 36px auto 48px;
  }
}

@media screen and (max-width: 580px) {
.elements { 
    grid-template-columns: 1fr;
    max-width: 282px;
    min-height: 2268px;
  }
}

@media screen and (max-width: 320px) { 
.elements {
    margin: 36px 19px 48px;
  }
}
.element {
    max-width: 282px;
    max-height: 361px;
    background: #FFFFFF;
    border-radius: 10px;
    display: grid;
    grid-template-rows: repeat(1, 1fr);
    grid-template-columns: repeat(auto-fit, minmax(282px, 1fr));
  }
.element__image {
    width: 100%;
    height: 282px;
    border-radius: 10px 10px 0 0;
    object-fit: cover;
    cursor: pointer;
    transition: opacity 0.2s ease;
  }
.element__conteiner {
    display: flex;
    justify-content: space-between;
    max-width: 282px;
    margin: 25px 15px 28px 21px;
    max-height: 28px;
  }
.element__title {
    max-width: 246px;
    margin: 0;
    font-style: normal;
    font-weight: 900;
    font-size: 24px;
    line-height: 29px;
    color: #000000;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
  }
.element__like-button {
    outline: none;
    background: none;
    border: none;
    width: 22px;
    height: 19px;
    align-self: center;
    cursor: pointer;
    flex-shrink: 0;
    transition: opacity 0.2s ease;
    background-image: url(/mesto-react/static/media/like.00b1c22a.svg);
    margin: 0 0 2px;
    background-size: cover;
  }

.element__like-button:hover {
    opacity: 0.5;
  }
.element__like-button_active {
    background-image: url(/mesto-react/static/media/Union.3d813f81.svg);
  }
.element__delete-button {
  outline: none;
    background: none;
    border: none;
    position: absolute;
    margin-top: 20px;
   margin-left: 244px;
    width: 18px;
    height: 19px;
    background-image: url(/mesto-react/static/media/Trash.4831282e.svg);
    background-repeat: no-repeat;
    cursor: pointer;
    transition: opacity 0.2s ease;
  }

.element__delete-button:hover {
    opacity: 0.5;
  }
