.popup {
    position: fixed;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 200;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    opacity: 0;
    visibility: hidden;
    transition: visibility .3s, opacity .3s linear;
  }
.popup_opened {
    display: flex;
    visibility: visible;
    opacity: 1;
  }
.popup__window {
    position: relative;
    margin: auto;
  }
.popup_type_image {
    background-color: rgb(0, 0 , 0, .9);
  }
.popup__title {
    margin: 34px 64px 54px 36px;
    font-style: normal;
    font-weight: 900;
    font-size: 24px;
    line-height: 29px;
    color: #000000;
  }

@media screen and (max-width: 570px) {
.popup__title {
    margin: 25px 22px 45px 22px;
    font-size: 18px;
    line-height: 22px;
  }
}
.popup__form {
    background-color: #FFFFFF;
    box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
    border-radius: 10px;
    display: flex;
    margin: auto;
    min-width: 430px;
    flex-direction: column;
  }
  
  @media screen and (max-width: 570px) {
    .popup__form  {
      min-width: 282px;
      }
    }

.popup__input {
    border: 0;
    border-bottom: 1px solid rgb(0,0,0, 0.2);
    outline: none;
    padding: 0;
    padding-bottom: 13px;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 17px;
    color: #000000;
    margin-left: 36px;
    margin-right: 36px;
}

@media screen and (max-width: 570px) {
.popup__input {
    margin-left: 22px;
    margin-right: 22px;
  }
}
.popup__save-button {
    border: none;
    background: none;
    outline: none;
    padding: 0;
    min-height: 50px;
    margin: 18px 36px 37px;
    background-color: #000000;
    border-radius: 2px;
    cursor: pointer;
